    font-size: 0.9375rem;
    background-color: #0075dd;
    border-radius: 0.5rem;
    transform: translateX(-50%);
    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
  }
}
@media (min-width: 768px) {
  .tooltip-blue::after {
    position: absolute;
    top: 98%;
    left: 50%;
